@import "index.module.scss";

.navigationMobile {
  display: none;
}

.navigationPC {
  width: 240px;
  float: left;
  .top {
    overflow: hidden;
    border-radius: 5px;
    padding: 5px 5px 0;
    background-color: $theme-content-bg;
    box-shadow: 0px 3px 12px -2px rgba(157, 129, 51, 0.8) inset;
    font-family: $theme-ff;
  }
  .fixed {
    width: 240px;
    position: fixed;
    top: -15px;
  }
  .bottom {
    background-color: $theme-content-bg;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.5) inset;
    margin-top: 15px;
    overflow: hidden;
    .name {
      padding-top: 30px;
      text-align: center;
      color: black;
      font-weight: bold;
      font-size: 25px;
      margin-bottom: 15px;
      .subName {
        font-size: 18px;
      }
    }
    .slogan {
      font-size: 18px;
      text-align: center;
      margin-bottom: 20px;
      font-weight: bold;
      background: $theme-color url("../../../assets/image/text-bg.jpg");
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
    }

    .articleType {
      text-align: center;
      padding: 15px 0;
      cursor: pointer;
      font-size: 16px;
      font-family: $theme-ff;
    }
    .subT {
      display: block;
      font-size: 12px;
      margin-top: 8px;
      overflow: hidden;
      transform: scale3d(0, 0, 0);
      opacity: 0;
    }
    .articleType.active,
    .articleType:hover {
      background-color: rgba(239, 239, 239, 0.5);
      color: $theme-color;
      font-weight: bold;
      box-shadow: 0px 0px 5px -2px rgba(0, 0, 0, 0.8) inset;
      span {
        background: $theme-color url("../../../assets/image/text-bg.jpg");
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
      }
      .subT {
        transform: scale3d(1, 1, 1);
        opacity: 0.9;
        transition: 0.6s cubic-bezier(0.17, 0.14, 0.5, 0.86);
        font-family: "微软雅黑";
      }
    }

    .linkBox {
      padding-top: 15px;
      padding-bottom: 15px;
      display: flex;
      justify-content: space-evenly;
      .link {
        width: 25px;
        height: 25px;
      }
    }
  }
  .bottomDetail {
    padding: 20px 0;
    max-height: 55vh;
    overflow-y: auto;
    .outline {
      cursor: pointer;
      padding: 10px 10px;
      span > span {
        display: inline-block;
        margin-left: 20px;
      }
    }
    .outline:hover,
    .outline.active {
      background-color: rgba(239, 239, 239, 0.5);
      color: $theme-color;
      font-weight: bold;
      box-shadow: 0px 0px 5px -2px rgba(0, 0, 0, 0.8) inset;
      span {
        background: $theme-color url("../../../assets/image/text-bg.jpg");
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
      }
    }
  }
  .back {
    text-align: center;
    cursor: pointer;
    padding: 10px 0;
    background-color: #eef0f4;
    box-shadow: 0px 2px 4px 1px rgba(0, 0, 0, 0.5);
    letter-spacing: 5px;
  }
}

.rightBox {
  background-color: $theme-color-bd;
  margin-left: 260px;
}

/* 移动端样式*/
@media screen and (max-width: $content-width) {
  .navigationPC {
    display: none;
  }
  .navigationMobile {
    display: block;
    background: rgba(255, 255, 255, 1);
    color: $theme-color;
    text-align: center;
    position: fixed;
    width: 100%;
    z-index: 999;
    box-shadow: 0 0 0.05rem -0.02rem rgba(0, 0, 0, 0.8);
    .mobile {
      font-size: 0.22rem;
      text-align: center;
      font-weight: bold;
      background: $theme-color url("../../../assets/image/text-bg.jpg");
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
      padding: 0.05rem 0;
      border-bottom: 1px solid rgba(200, 200, 200, 1);
      .t {
        padding: 0.2rem 0;
      }
      .logo {
        width: 2rem;
      }
    }
    .mobileNav {
      display: flex;
      justify-content: space-around;
      a {
        flex: 1;
      }
      .articleType {
        text-align: center;
        color: black;
        padding: 0.15rem 0;
        font-size: 0.16rem;
      }
      .articleType.active {
        background-color: rgba(239, 239, 239, 0.5);
        color: $theme-color;
        font-weight: bold;
        box-shadow: 0px 0px 0.05rem -0.02rem rgba(0, 0, 0, 0.8) inset;
        span {
          background: $theme-color url("../../../assets/image/text-bg.jpg");
          background-clip: text;
          -webkit-background-clip: text;
          color: transparent;
        }
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .navigationMobile {
    .mobile {
      font-size: 0.45rem;
      .t {
        padding: 0.4rem 0;
      }
      .logo {
        width: 3rem;
      }
    }
    .mobileNav {
      .articleType {
        padding: 0.2rem 0;
        font-size: 0.25rem;
      }
    }
  }
}
